import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
import uniPopup from "@/components/uni/uni-popup/uni-popup.vue"
import robbyImageUpload from '@/components/plan-image-upload/up_img'
import {
	Api_url
} from '@/common/config'
export default {
	data() {
		return {
			groupList: [],
			form: {
				id: '',
				name: ''
			},
			name: '',
			id: '4',
			group: '',
			group_id: ''
		};
	},
	components: {
		uniIcon,
		uniPopup,
		robbyImageUpload
	},
	computed: {},
	watch: {},
	onLoad() {
		this.get_groupList()
		
	},
	onShow() {},
	methods: {
		get_groupList(){
			this.groupList=this.$api.json_cms.get_category
		},
		sub() {
			console.log(this.groupList)
			let group_cache = {
				groupList: this.groupList,
				group_id: this.group_id,
				group_name: this.group
			}
			uni.setStorageSync('groupList', group_cache)
			uni.navigateBack({

			})
		},
		group_change(event) {
			const that = this
			const li = event.target.value
			console.log(event.target.value)
			let arr = []
			let arr2 = []
			for (let x in li) {
				let v = li[x]
				arr[x] = that.groupList[v].id + ''
				arr2[x] = that.groupList[v].name + ''
			}
			this.group = arr2.join(" ")
			this.group_id = arr
			console.log(this.group)
			console.log(this.group_id)
			uni.setStorageSync('group_id',this.group_id)
		},
	},
	onPullDownRefresh() {
		this.get_groupList()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
